@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eeeeee
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #eeeeee;
  width: 100%;
  height: 100%; }

.Navbar {
  background-color: #FFE600;
  width: 100%;
  height: 9vmin;
  vertical-align: middle; }

.Logo {
  display: inline-block;
  width: 10vmin;
  margin-left: 15%;
  margin-right: 2%;
  margin-top: 0.5%; }

.Searcher {
  display: inline-block;
  background-color: #FFF;
  color: #999999;
  border: none;
  border-radius: 5px;
  vertical-align: super; }

.Inside {
  display: inline-block;
  width: 50vmax;
  font-size: 18px;
  padding: 1%; }

.Results {
  width: 100%;
  height: 100%;
  background-color: #eeeeee;
  align-content: center;
  justify-content: center; }

.card {
  background: #FFF;
  width: 80vw;
  height: 200px;
  margin: 0.1% 10%;
  color: #000; }

.imageCard, .infoCard, .cityCard {
  display: inline-block; }

.imageCard {
  width: 180px;
  height: 180px;
  margin: 16px;
  border-radius: 4px; }

.infoCard {
  width: 40vmax;
  vertical-align: top;
  margin: 2% 2%;
  font-size: 18px;
  color: #333333; }

.cityCard {
  align-content: center;
  vertical-align: top;
  margin-top: 5%;
  font-size: 12px;
  color: #999999; }

.Price {
  font-size: 24px;
  display: inline-block; }

.shipping {
  width: 24px;
  display: inline-block; }

.breadcrumb {
  display: inline-block;
  color: #666666; }

.breadcrumbItem {
  margin: 16px 10%; }

.divOutside {
  width: 100%;
  height: 100%;
  position: static;
  top: 60px;
  left: 0;
  z-index: 100;
  background-color: #eeeeee;
  display: flex; }

.divInside {
  margin: auto auto;
  width: 90%;
  margin-top: 30px;
  background: white;
  color: black;
  display: block; }

.divImg {
  width: 65%;
  box-sizing: border-box;
  display: inline-block;
  overflow: visible; }

.imgModal {
  width: 680px;
  display: inline-block;
  margin: 2%; }

.divText {
  width: 35%;
  display: inline-block;
  box-sizing: content-box;
  overflow: visible;
  vertical-align: top;
  margin: 32px auto; }

.infoSold {
  font-size: 14px;
  color: #666666; }

.title {
  font-size: 24px;
  color: #333333;
  margin-top: 16px;
  margin-right: 16px; }

.price {
  font-size: 46px;
  color: #333333;
  margin: 32px auto; }

.buy {
  background-color: #3483FA;
  color: #eeeeee;
  padding: 2% 15%;
  border: none;
  border-radius: 4px; }

.divDescription {
  width: 65%;
  display: block;
  margin: auto 32px;
  text-align: justify;
  font-size: 16px;
  color: #666666; }

.titleDescription {
  font-size: 28px;
  color: #333333; }

